<template>
  <div style="width:1150px;">
    <!---->
    <div class="searchbox">
      <span class="search-title"></span>
      <el-input v-model="title" class="input" placeholder=""></el-input>
      <span class="search-title"></span>
      <el-select v-model="day" placeholder="" class="input">
        <el-option v-for="item in days" :key="item.value" :label="item.label":value="item.value"></el-option>
      </el-select>
      <span class="search-title"></span>
      <el-date-picker v-model="startDate" type="date" placeholder="" class="input_01" @change="endDateChange"></el-date-picker>
      <span class="dateline"></span>
      <el-date-picker v-model="endDate" type="date" placeholder="" class="input_01" @change="endDateChange"></el-date-picker>
      <!---->
      <span class="search-title por"><span v-if="isPod" class="poa"></span></span>
      <el-autocomplete class="input" v-model="pod" :fetch-suggestions="querySearch" :trigger-on-focus="false" @select="departure1" @blur="isToastFun(1)" placeholder=""></el-autocomplete>
      <!---->
      <span class="search-title por"><span v-if="isDes" class="poa"></span></span>
      <el-autocomplete class="input" v-model="destination" :fetch-suggestions="querySearch" :trigger-on-focus="false" @select="departure2" @blur="isToastFun(2)" placeholder=""></el-autocomplete>
      <!---->
      <span class="search-title"></span>
      <el-select v-model="productType" placeholder="" class="input">
        <el-option v-for="item in productGenre" :key="item.value" :label="item.label":value="item.value"></el-option>
      </el-select>
    </div>
    <!---->
    <div class="productButton">
      <el-button type="primary" @click="list(1,pageSize)"></el-button>
      <el-button type="primary" @click="empty()"></el-button>
    </div>
    <!---->
    <div class="switch">
      <div class="switchText"></div>
      <el-switch class="switchButton" v-model="customerPrice"></el-switch>
    </div>
    <!---->
    <el-table :data="tableData"  class="labelTable" ref="multipleTable" :header-cell-style="getRowClass" border>
      <el-table-column prop="id" label="ID" width="80" align="center"></el-table-column>
      <el-table-column prop="title" label="" width="249" align="center"></el-table-column>
      <el-table-column prop="productType" label="" width="150" align="center">
         <template slot-scope="scope">
            <span v-show="scope.row.productType == 1"></span>
         </template>
      </el-table-column>
      <el-table-column prop="place" label="/" width="180" align="center"></el-table-column>
      <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">
            <span v-if="customerPrice == true">{{scope.row.minPrice01}}</span>
            <span v-if="customerPrice == false">{{scope.row.minPrice02}}</span>
          </template>
      </el-table-column>
      <el-table-column prop="day" label="" width="80" align="center"></el-table-column>
      <el-table-column prop="date" label="" width="120" align="center"></el-table-column>
      <el-table-column label="" width="120" align="center">
        <template slot-scope="scope">
          <el-button @click="checkIncome(scope.row.id)" type="text" size="small" class="table_details"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination v-if="pageshow" class="pagination"
      @size-change="handleSizeChange"
      background
      @current-change="handleCurrentChange"
      :current-page="1" 
      :page-sizes="[10, 30, 50, 100]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        //
        title:'',//
        day:'',//
        days:[{//
          value: 1,
          label: '1'
        }, {
          value: 2,
          label: '2'
        }, {
          value: 3,
          label: '3'
        }, {
          value: 4,
          label: '4'
        }, {
          value: 5,
          label: '5'
        }, {
          value: 6,
          label: '6'
        }, {
          value: 7,
          label: '7'
        }, {
          value: 8,
          label: '8'
        }, {
          value: 9,
          label: '8'
        }],
        startDate:'',//
        endDate:'',//
        pod:'',
        podID:0,//
        destination:'',
        destinationID:0,//
        isPod:false,
        isDes:false,
        productType:'',//
        productGenre:[{//
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }],
        customerPrice:false,
        tableData:[],
        //
        pageshow:true,
        pageSize: 10, // 
        pageIndex: 1, // 
        total: 0,
      }
    },
    methods:{
      empty(){//
        this.title = '';
        this.day = '';
        this.startDate = '';
        this.endDate = '';
        this.pod = '';
        this.podID = 0;
        this.destination = '';
        this.destinationID = 0;
        this.productType = '';
      },
      getRowClass({ row, column, rowIndex, columnIndex }) {//
        if (rowIndex == 0) {
          return 'background:#f7f7f7;height:60px;textAlign:center;color:#333;fontSize:15px'
        } else {
          return ''
        }
      },
      //
      handleSizeChange(val){
        this.pageSize = val;
        this.pageIndex = 1;
        this.list(1,val);
      },
      handleCurrentChange(val){
        this.list(val,this.pageSize);
        this.pageIndex=val;
      },
      checkIncome(id){
        this.$router.push({
          path: '/productDetails',
          query: {
              id: id
          }
        })
      },
      //
      list(pageIndex=this.pageIndex,pageSize=this.pageSize,title=this.title,day=this.day,startDate=this.startDate,endDate=this.endDate,podID=this.podID,destinationID=this.destinationID,productType=this.productType){
        if(startDate){
          let y=startDate.getFullYear();
          let m=(startDate.getMonth()+1)>9?startDate.getMonth()+1:'0'+(startDate.getMonth()+1);
          let d=startDate.getDate()>9?startDate.getDate():'0'+startDate.getDate();
          startDate=''+ y + m + d
        }else{
          startDate = 0;
        }
        if(endDate){
          let y=endDate.getFullYear();
          let m=(endDate.getMonth()+1)>9?endDate.getMonth()+1:'0'+(endDate.getMonth()+1);
          let d=endDate.getDate()>9?endDate.getDate():'0'+endDate.getDate();
          endDate=''+ y + m + d
        }else{
          endDate = 0;
        }
        this.$http.post(this.GLOBAL.serverSrc + "/indirect/team/pc/page",{
            "pageIndex":pageIndex,
            "pageSize":pageSize,
            "object": {
                "title": title,
                "day": day?day:0,
                "startDate": startDate,
                "endDate": endDate,
                "podID": podID,
                "destinationID": destinationID,
                "productType": productType?productType:0,
            }
         })
        .then(res => {
            this.tableData=[];
            this.tableData = res.data.objects;
            this.total = res.data.total;
            this.tableData.forEach(v => {
              v.date = v.date.toString();
              let date = v.date.substr(0,4) + '-' + v.date.substr(4,2) + '-' + v.date.substr(6,2);
              v.date=date;
            });
            this.$nextTick(() => {
                this.pageshow = true;
            })
        })
      },
       //
    querySearch(queryString, cb) {
      this.$http.post(this.GLOBAL.serverSrc + '/indirect/universal/area/fuzzy', {
        "object": {
          areaName: queryString
        }
      }).then(res => {
         if (res.data.objects.length > 0) {
          let tableData=[];
          for(let i=0;i<res.data.objects.length;i++){
            tableData.push({
              "value" : res.data.objects[i].areaName,
              "id":res.data.objects[i].id
            })
          }
          var results = queryString ? tableData.filter(this.createFilter(queryString)) : [];
          cb(results)
         }else{
            this.isDes = true;
            this.isPod = true;
            cb && cb([]);
          }
        }).catch(err => {
          console.log(err);
        })
      },
      createFilter(queryString){
        return (restaurant) => {
          return (restaurant.value);
        }
      },
      departure1(item){
        this.podID = item.id;
        this.pod = item.value;
      },
      departure2(item){
        this.destinationID = item.id;
        this.destination = item.value;
      },
      isToastFun(i){
        if(i==1){
          this.isDes = false;
          this.pod = "";
          this.podID = 0;
        }else{
          this.isPod = false;
          this.destination = "";
          this.destinationID = 0;
        }
      },
      endDateChange() {
        let startDate = new Date(this.startDate).getTime();
        let endDate = new Date(this.endDate).getTime();
        if (this.startDate !== "") {
          if (endDate < startDate) {
            this.$message.error("");
            this.endDate = "";
          }
        }
      }
    },
    mounted(){
      this.list(1,this.pageSize);
    }
  }



</script>

<style scoped>
  /**/
  .searchbox{width: 1150px;}
  .input{margin:10px 5px;width: 200px;}
  .input_01{margin:10px 5px;width: 150px;}
  .search-title{font-size: 14px;margin-left:5px;display: inline-block;width: 100px;text-align: right;}
  .dateline{ margin: 0 5px 0 5px; }
  .productButton{overflow: hidden; margin: 10px 20px 0 0;display: inline-block; float: right;}
  .poa{position: absolute;left: 76px;top: 30px;width: 100px;color: red;font-size: 12px}
  .por{position: relative}
  /**/
  .switch{ clear: both; padding: 10px 0 20px 0;  overflow: hidden;}
  .switchText{float: left; width: 100px; text-align: right;}
  .switchButton{float: left; margin: 0 0 0 15px;}
  /**/
  .labelTable{margin: 0 30px 20px 30px; text-align: center;max-width: 1100px;}
  /**/
  .pagination{text-align:center;margin:40px 0}
</style>




