<template>
  <div>
       <!---->
       <el-dialog title="" :visible.sync="dialogFormMark" class="city_list" width="800px" style="margin-top:-100px">
          <table border="0" cellpadding="7" cellspacing="10" bgcolor="#f7f7f7" width="760" style="margin-top:-10px">
              <tr>
                  <td></td>
                  <td>{{orderForm.name}}</td>
              </tr>
              <tr>
                  <td>ID</td>
                  <td>{{orderForm.orderCode}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.groupCode}}</td>
              </tr> 
              <tr>
                  <td></td>
                  <td>{{formatDate(new Date(orderForm.createTime))}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.planTime}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td v-show="orderForm.orderStatus == 1"></td>
                  <td v-show="orderForm.orderStatus == 2"></td>
                  <td v-show="orderForm.orderStatus == 3"></td>
                  <td v-show="orderForm.orderStatus == 4"></td>
                  <td v-show="orderForm.orderStatus == 5"></td>
                  <td v-show="orderForm.orderStatus == 6"></td>
                  <td v-show="orderForm.orderStatus == 0 && orderForm.occupyStatus == 1"></span>
                  <td v-show="orderForm.orderStatus == 0 && orderForm.occupyStatus == 2"></span>
                  <td v-show="orderForm.orderStatus == 0 && orderForm.occupyStatus == 3"></span>
                  <td v-show="orderForm.orderStatus == 8"></td>
                  <td v-show="orderForm.orderStatus == 9"></td>
                  <td v-show="orderForm.orderStatus == 10"></td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.package}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.pod}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.destination}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.op}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.saler}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{JSON.parse(orderForm.contact).Name}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{JSON.parse(orderForm.contact).Tel}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.enrollDetail}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td><span class="table_span" v-show="index == 0" v-for="(item, index) in orderForm.favourable" :key="index">{{item.title}} {{item.price}}</span></td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.payable}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{orderForm.payable - orderForm.paid}}</td>
              </tr>
              <tr>
                  <td></td>
                  <td>{{formatDate(new Date(orderForm.repaymentDate))}}</td>
              </tr>
          </table>
          <el-table :data="orderForm.guests" ref="multipleTable" class="table" :header-cell-style="getRowClass" border :cell-style="getCellClass">     
             <el-table-column  prop="cnName" label="" width="100"></el-table-column>
             <el-table-column  prop="enrollName" label="" width="160"></el-table-column>
             <el-table-column  prop="mobile" label="" width="150"></el-table-column>
             <el-table-column  prop="idCard" label="" min-width="200"></el-table-column>
             <el-table-column  prop="sex" label="" width="100">
                <template slot-scope="scope">
                     <span v-if="scope.row.sex===0"></span>
                     <span v-if="scope.row.sex===1"></span>
                     <span v-if="scope.row.sex===3"></span>
                </template>
             </el-table-column>
           </el-table>
           <div class="cancel"><el-button @click="close">&nbsp;&nbsp;</el-button></div>
       </el-dialog>
  </div>
</template>

<script>
export default {
  props:{
    orderData: '',
    variable:0,
    dialogType:0,
  },
  data() {
    return {
      //
      dialogFormMark:false,
      orderForm:{contact:"{}"}
    }
  },
  created(){
  },
  watch: {
    variable:function(){        
      if(this.dialogType==1){
        this.dialogFormMark=true;    
        this.getOrderData();
      }
    }
  },
  methods: {
    formatDate(date){
       var y = date.getFullYear();  
       var m = date.getMonth() + 1;  
           m = m < 10 ? ('0' + m) : m;  
       var d = date.getDate();  
           d = d < 10 ? ('0' + d) : d;  
       var h = date.getHours();  
           h=h < 10 ? ('0' + h) : h;  
       var minute = date.getMinutes();  
           minute = minute < 10 ? ('0' + minute) : minute;  
       var second=date.getSeconds();  
           second=second < 10 ? ('0' + second) : second;  
           return y + '-' + m + '-' + d +' '+ h + ':' + minute + ':' + second;
    },
    getOrderData(){
      this.$http.post(this.GLOBAL.serverSrc + '/indirect/orderquery/get/siorders', {
        "orderCode": this.orderData.orderCode,
        "id": this.orderData.id
      }).then(res => {
        this.orderForm = res.data.object;
        let planTime = res.data.object.planTime + '';
        this.orderForm.planTime = planTime.substr(0,4) + '-' + planTime.substr(4,2) + '-' + planTime.substr(6,2);
      })
    },
    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (rowIndex == 0) {
        return 'background:#f7f7f7;height:50px;textAlign:center;color:#333;fontSize:15px'
      } else {
        return ''
      }
    },
    getCellClass(){
      return 'textAlign:center'
    },
    close(){
      this.dialogFormMark=false;
    },
    }
}
</script>

<style scoped>
   .demo-ruleForm{margin-top: 20px}
   .remark{width: 600px}
   .time{margin-right: 60px;text-align: right;color: #999}
   .submitMark{float: right;margin-right: 60px}
   .colse{margin-left: 230px;width: 100px}
   .table{margin-top: 15px}
   .cancel{margin: 15px 0 0 0;text-align: right;}
   .table_span:not(:first-child){margin-left: 10px;}
</style>


